import { Button, notification } from 'antd';
import React, { useEffect, useRef } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { useReactToPrint } from 'react-to-print';
import actions from '../../../redux/transaction/actions';
import { PrintPage } from '../PrintPage';

export default function SavePendingButton({ isDisabled = false }) {
	const dispatch = useDispatch();
	const history = useHistory();
	let componentPrintRef = useRef(null);

	const { data = [], loading = false, billNo = '', billDate = '' } = useSelector((state) => state?.transaction);
	const { property = {}, cashier = {} } = JSON.parse(localStorage.getItem('app') || '{}');
	const isOpeningShift = cashier?.isOpening || false;

	const disabled = isDisabled || data?.length === 0 || loading || !isOpeningShift;

	const handlePendingPayment = () => {
		const subTotal = data.reduce((a, { total }) => +a + +total, 0);

		const header = [
			{
				billNo,
				billDate,
				custCode: '',
				custName: '',
				discPrc: '0',
				discValue: '0',
				taxPrc: '0',
				taxValue: '0',
				subTotal: subTotal,
				grandTotal: subTotal,
				shiftID: cashier?.shiftId,
				outletCode: property?.outletCode
			}
		];

		const payload = {
			header,
			details: data?.map((el, i) => ({ ...el, billNo, ItemNo: i + 1 }))
		};

		dispatch({
			type: 'transaction/HOLD',
			payload,
			history,
			handlePrint: handlePrint(null, () => componentPrintRef)
		});
	};

	const onKeyDownGlobal = (e) => {
		if (e.key === 'F6') {
			e.preventDefault();

			if (!disabled) {
				handlePendingPayment();
			}
		}
	};

	const handlePrint = useReactToPrint({
		onPrintError: (location, error) => {
			console.log('Error Print!', error);

			notification.error({
				message: 'Print Struk!',
				description: `Gagal mencetak struk. ${location} - ${error}`
			});

			dispatch({
				type: actions.SET_STATE,
				payload: {
					loading: false
				}
			});
		},
		onAfterPrint: () => {
			notification.success({
				message: 'Print!',
				description: 'Cetak struk berhasil.'
			});
		}
	});

	useEffect(() => {
		document.addEventListener('keydown', onKeyDownGlobal);

		return () => {
			document.removeEventListener('keydown', onKeyDownGlobal);
		};
	}, [data]);

	return (
		<>
			<Button loading={loading} disabled={disabled} className="btn-page" onClick={handlePendingPayment}>
				<kbd className="kbd">f6</kbd>
				<p>simpan pending</p>
			</Button>

			<div className="w-full h-full hidden">
				<PrintPage
					ref={(el) => (componentPrintRef = el)}
					transactions={data}
					cash={{
						cashChange: 0,
						cashPaid: 0,
						total: 0
					}}
					billDate={new Date()}
					payments={[]}
					paymentMethod={''}
					isPendingBill={true}
				/>
			</div>
		</>
	);
}
